/* @import must be at top of file, otherwise CSS will not work */
@import url("//hello.myfonts.net/count/3487b2");


@font-face {
  font-family: 'MorebiRounded-Light';
  src: url('../fonts/webfonts/3487B2_0_0.eot');
  src: url('../fonts/webfonts/3487B2_0_0.eot?#iefix') format('embedded-opentype');
}
@font-face {
  font-family: 'MorebiRounded-Light';
  src:url('data:application/font-woff2;base64,') format('woff2'),
  url('data:application/font-woff;base64,') format('woff'),
  url('data:font/opentype;base64,') format('truetype'),
  url('../fonts/webfonts/3487B2_0_0.svg#wf') format('svg');
}

h1, h2, h3, h4, h5, h6 {
  font-family: MorebiRounded-Light;
}

h1 {
  font-size: 48px;
  line-height: 40px;
  letter-spacing: -2px;
}

h2 {
  font-size: 40px;
  line-height: 32px;
  letter-spacing: -2px;
  margin-bottom: 20px;
  color: $primary-blue;
}

h3 {
  font-size: 32px;
  line-height: 28px;
  letter-spacing: -2px;
}

h4 {
  font-size: 26px;
  line-height: 24px;
  letter-spacing: -2px;
}

body {
  font-family: "museo-sans";
  font-size: 16px;
  line-height: 20px;
}

h1.page-header {
  text-align: center;
  padding: 20px 0;
  background-color: $primary-blue;
  color: whitesmoke;
}